input[type='text'],
input[type='tel'],
input[type='email'],
input[type='search'],
input[type='password'],
input[type='url'],
textarea {
	display: block;
	width: 100%;
	font-size: $input-font-size;
	line-height: $input-line-height;
	font-family: $input-font-family;
	background-color: $white;
	border: $input-border;
	border-radius: $input-border-radius;
	padding: $input-padding;
	color: $input-color;
	text-overflow: ellipsis;
	transition: border-color 0.3s;

	&:hover {
		border-color: darken($input-border-color, 15%);
	}

	&:focus {
		border-color: $primary;
	}

	@include placeholder {
		color: $input-placeholder-color;
	}

	&:not(textarea) {
		height: $input-height;
	}
}

button,
input[type='button'],
input[type='reset'],
input[type='file'],
input[type='submit'] {
	@extend .btn;
	@extend .btn-primary;

	cursor: pointer;
}

.email-form {
	position: relative;
	max-width: 455px;

	@include max-w(sm) {
		text-align: center;
	}

	&__input {
		margin-bottom: 10px;

		@include min-w(sm) {
			padding-right: 156px !important;
			margin-bottom: 0;
		}
	}

	&__btn {
		@include min-w(sm) {
			position: absolute;
			top: 50%;
			right: $input-padding-horizontal;
			transform: translate(0, -50%);
			z-index: 5;
		}
	}
}