.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: $zindex-fixed;
	padding: 20px 0;
	transition: padding 0.3s, background-color 0.3s;

	@include min-w(md) {
		padding: 30px 0;
	}

	@include min-w(1600) {
		padding: 50px 0;
	}

	&.active {
		background-color: $white;

		@include min-w(md) {
			padding: 20px 0;
		}

		@include min-w(1600) {
			padding: 25px 0;
		}
	}

	&__logo {
		display: flex;
		max-width: 150px;

		@include min-w(md) {
			max-width: 190px;
		}
	}
}