@include title-sizes();

.text-block {
	p:last-child {
		margin-bottom: 0;
	}
}

.text-enlarged {
	font-size: 18px;

	@include min-w(md) {
		font-size: 20px;
	}

	@include min-w(lg) {
		font-size: 22px;
	}
}

strong {
	font-weight: 600;
}

p {
	margin-top: 0;
	margin-bottom: 15px;
}

h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6 {
	font-family: $headings-font-family;
	color: $headings-color;
	font-weight: 700;
	margin-top: 0;
}

h1, .h1 {
	margin-bottom: 20px;

	@include min-w(lg) {
		margin-bottom: 25px;
	}
}

h2, .h2 {
	margin-bottom: 20px;

	@include min-w(lg) {
		margin-bottom: 25px;
	}
}

h3, .h3 {
	margin-bottom: 20px;
}

h4, .h4 {
	margin-bottom: 20px;
}

h5, .h5 {
	margin-bottom: 15px;
}

h6, .h6 {
	margin-bottom: 15px;
}