*, *:before, *:after {
  box-sizing: border-box;
}

* ,
*:hover ,
*:focus ,
*:active {
	outline: none !important;
}

body {
	text-rendering: optimizeLegibility;
	-webkit-font-smoothing: antialiased;
	overflow-x: hidden;
}

input[type='text'],
input[type='tel'],
input[type='email'],
input[type='search'],
input[type='password'],
input[type='url'],
textarea {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;

	@include placeholder {
		line-height: normal;
		transition: opacity 0.3s;
	};

	&:focus {
		@include placeholder {
			opacity: 0;
		};
	}
}

textarea {
	resize: none;
	overflow-y: auto;
	overflow-x: hidden;
}

// hide cross icon in IE
::-ms-clear {
	display: none;
}

.btn:focus,
.btn.focus,
.btn:active,
.btn:visited {
	box-shadow: none;
}

a {
	&[href*="mailto:"] {
		word-wrap: break-word;
	}
}

img {
	max-width: 100%;
	height: auto;
}