// COLORS =================================
$white:												#FFFFFF !default;
$gray-900:										#1F342F !default;
$gray-500:										#68768B !default;
$gray-400:										#D4E1F2 !default;
$gray-100:										#F2F6FB !default;
$black:												#000000 !default;
$green:												#3CEAC1 !default;

$primary:											$green !default;
$secondary:										$gray-900 !default;

// BASE =================================
$base-font-sans-serif:				"Public Sans", "Arial", "Helvetica Neue", "Helvetica", sans-serif !default;
$base-font-family:						$base-font-sans-serif !default;

$base-text-color:							$gray-500 !default;
$base-background-body:				$gray-100 !default;
$font-size-base:							16px !default;
$line-height-base:						1.3 !default;
$base-min-width:							320px;

$max-width-center-container:	1200px !default;
$gutter:											15px !default;
$padding-container:						0 $gutter !default;

// Liks
$base-link-color:							inherit !default;
$base-link-hover-color:				$primary !default;
$text-decoration:							underline !default;
$text-decoration-hover:				none !default;

// Forms
$input-font-size:							$font-size-base !default;
$input-line-height:						$input-font-size  + 2 !default;
$input-font-family:						$base-font-family !default;
$input-padding-vertical:			26px !default;
$input-padding-horizontal:		16px !default;
$input-padding:								$input-padding-vertical $input-padding-horizontal !default;
$input-border-width:					1px !default;
$input-border-color:					$gray-400 !default;
$input-border:								$input-border-width solid $input-border-color !default;
$input-border-radius:					4px !default;
$input-height:								(2 * ($input-border-width + $input-padding-vertical) + $input-line-height) !default;
$input-color:									$base-text-color;
$input-placeholder-color:			$base-text-color;

$grid-breakpoints: (
	xs: 0,
	sm: 576px,
	md: 768px,
	lg: 992px,
	xl: 1200px
) !default;

// Headings
$headings-font-family:				"Mulish", "Arial", "Helvetica Neue", "Helvetica", sans-serif !default;
$headings-color:							$secondary !default;

$title-palette: (
	xs: (
		'h1': 35,
		'h2': 32,
		'h3': 28,
		'h4': 24,
		'h5': 20,
		'h6': 18
	),
	sm: (
		'h1': 45,
		'h2': 35,
		'h3': 30,
		'h4': 25,
		'h5': 20,
		'h6': 18
	),
	md: (
		'h1': 60,
		'h2': 40,
		'h3': 35,
		'h4': 30,
		'h5': 25,
		'h6': 18
	),
	lg: (
		'h1': 80,
		'h2': 60,
		'h3': 45,
		'h4': 30,
		'h5': 25,
		'h6': 18
	),
	xl: (
		'h1': 100,
		'h2': 80,
		'h3': 60,
		'h4': 40,
		'h5': 30,
		'h6': 20
	)
) !default;

// Z-index
$zindex-dropdown:                   1000 !default;
$zindex-sticky:                     1020 !default;
$zindex-fixed:                      1030 !default;
$zindex-offcanvas-backdrop:         1040 !default;
$zindex-offcanvas:                  1045 !default;
$zindex-modal-backdrop:             1050 !default;
$zindex-modal:                      1055 !default;
$zindex-popover:                    1070 !default;
$zindex-tooltip:                    1080 !default;