.section {
	&-coming-soon {
		min-height: 100vh;
		display: flex;
		align-items: center;
		background-image:
			url("../img/backgrounds/1.svg"),
			url("../img/backgrounds/2.svg")
		;
		background-repeat: no-repeat;
		background-size: 80px;
		background-position:
			left 0 top 90px,
			left bottom
		;
		padding: 100px 0 50px;

		@include min-w(sm) {
			background-size: 100px;
			background-position:
				left 0 top 100px,
				left bottom
			;
		}

		@include min-w(md) {
			background-size: 150px;
			background-position:
				left 0 top 127px,
				left bottom
			;
			padding: 100px 0;
		}

		@include min-w(xl) {
			background-size: 250px, 250px;
		}

		&__title-holder {
			text-align: center;
			margin-bottom: 40px;

			@include min-w(md) {
				margin-bottom: 50px;
			}
		}

		&__email-form {
			margin: 0 auto 30px;
		}

		&__text-description {
			text-align: center;
			margin-bottom: 45px;
		}

		&__img {
			max-width: 320px;
			margin: 0 auto;
			text-align: center;
		}
	}
}